/* 
  Font Icon 
  ------------------------------------------------
*/

@font-face {
  font-family: 'sunset-icon';
  src:  url('../fonts/sunset-icon.eot?mlc7oc');
  src:  url('../fonts/sunset-icon.eot?mlc7oc#iefix') format('embedded-opentype'),
    url('../fonts/sunset-icon.ttf?mlc7oc') format('truetype'),
    url('../fonts/sunset-icon.woff?mlc7oc') format('woff'),
    url('../fonts/sunset-icon.svg?mlc7oc#sunset-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

.sunset-icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sunset-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

